<script>
import withdrawTable from './components/withdraw-table.vue';
import index from './index';
import { financeList } from './api.js';

export default {
  data() {
    return {
      form: {
        status: '0',
        level: 1,
        type: 1,
        time: []
      }
    };
  },
  components: {
    withdrawTable
  },
  mixins: [index],
  mounted() {
    this.financeList();
  },
  methods: {
    filter() {
      this.replaceQuery();
    },
    financeList() {
      financeList().then(response => {});
    }
  },
  render() {
    const self = this;
    const level = [
      {
        label: '全部',
        value: 1
      },
      {
        label: '白银',
        value: 2
      },
      {
        label: '黄金',
        value: 3
      },
      {
        label: '白金',
        value: 4
      }
    ];
    const type = [
      {
        label: '全部',
        value: 1
      },
      {
        label: '自动转账',
        value: 2
      },
      {
        label: '提现到微信',
        value: 3
      },
      {
        label: '提现到支付宝',
        value: 4
      },
      {
        label: '提现到银行卡',
        value: 5
      }
    ];
    return (
      <div class="le-main">
        <el-form class="le-card" inline={true} label-width="126px">
          <el-form-item label="分销商搜索">
            <el-input placeholder="输入ID/昵称/姓名/手机号搜索" />
          </el-form-item>
          <el-form-item label="分销商等级">
            <el-select placeholder="请选择" v-model={self.form.level}>
              {level.map((item, index) => {
                return <el-option key={index} label={item.label} value={item.value} />;
              })}
            </el-select>
          </el-form-item>
          <el-form-item label="提现方式">
            <el-select placeholder="请选择" v-model={self.form.type}>
              {type.map((item, index) => {
                return <el-option key={index} label={item.label} value={item.value} />;
              })}
            </el-select>
          </el-form-item>
          <el-form-item label="申请时间">
            <el-date-picker
              size="small"
              type="datetimerange"
              range-separator="至"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
            />
          </el-form-item>
          <div class="le-button">
            <el-button type="primary" onClick={self.filter}>
              筛选
            </el-button>
            <el-button>清空</el-button>
          </div>
        </el-form>
        <el-tabs class="le-tabs" type="card" v-model={self.form.status}>
          {[
            { name: '全部', key: '0', lazy: false },
            { name: '待审核', key: '1', lazy: false },
            { name: '待打款', key: '2', lazy: false },
            { name: '已打款', key: '3', lazy: false },
            { name: '已拒绝', key: '4', lazy: false }
          ].map((item, index) => {
            return (
              <el-tab-pane lazy={item.lazy} label={item.name} name={item.key} key={index}>
                <withdrawTable />
              </el-tab-pane>
            );
          })}
        </el-tabs>
      </div>
    );
  }
};
</script>

<style lang="scss" scoped>
@import './index.less';

.le-card {
  margin-bottom: 24px;
  .le-button {
    margin-left: 120px;
  }
}
</style>
