<script>
export default {
  name: 'withdraw-table',
  data() {
    return {
      list: [
        {
          type: 1,
          status: 1
        },
        { type: 2, status: 2 },
        { type: 3, status: 2 },
        { type: 4, status: 3 },
        { type: 5, status: 1 },
        { type: 2, status: 2 },
        { type: 1, status: 4 }
      ],
      form: {
        page: 1,
        count: 1
      }
    };
  },
  render() {
    const self = this;
    return (
      <div class="le-main">
        <el-table data={self.list} class="le-table" row-class-name="le-table-row">
          <el-table-column
            label="分销商"
            width="288px"
            fixed="left"
            scopedSlots={{
              default: () => {
                return (
                  <div class="flex">
                    <div>
                      <el-avatar size={48} src="https://empty" error={() => true}>
                        <img src="https://cube.elemecdn.com/e/fd/0fc7d20532fdaf769a25683617711png.png" />
                      </el-avatar>
                    </div>
                    <div class="le-usercontent">
                      <div class="le-user">
                        <i class="le-icon le-icon-xiaochengxu"></i>
                        <i class="le-icon le-icon-wehcat"></i>
                        <span class="le-username">法外狂徒张三</span>
                      </div>
                      <div class="le-userinfor">
                        ID:55
                        <br />
                        孙悟空
                        <br />
                        17657866777
                      </div>
                    </div>
                  </div>
                );
              }
            }}
          />
          <el-table-column
            label="分销商等级"
            scopedSlots={{
              default: () => {
                return '白银';
              }
            }}
          />
          <el-table-column
            label="提现方式"
            scopedSlots={{
              default: scope => {
                const type = scope.row.type;
                return [
                  [
                    <div>
                      {(() => {
                        if (type === 2) {
                          return '自动转账';
                        } else if (type === 3) {
                          return '提现到微信';
                        } else if (type === 4) {
                          return '提现到支付宝';
                        } else if (type === 5) {
                          return '提现到银行卡';
                        }
                      })()}
                    </div>
                  ],
                  [
                    <div class="le-auxiliaryText">
                      账号：5629262@qq.com
                      <br />
                      姓名：唐伯虎
                    </div>
                  ]
                ];
              }
            }}
          />
          <el-table-column
            label="申请提现金额"
            scopedSlots={{
              default: () => {
                return [[<div>￥51216.00</div>], [<div class="le-auxiliaryText">(手续费：￥0.00)</div>]];
              }
            }}
          />
          <el-table-column
            label="实际打款金额"
            scopedSlots={{
              default: () => {
                return '￥51200.00';
              }
            }}
          />
          <el-table-column
            label="状态"
            scopedSlots={{
              default: scope => {
                const status = scope.row.status;
                let type = '';
                let text = '';
                if (status === 1) {
                  type = '';
                  text = '待审核';
                } else if (status === 2) {
                  type = 'warning';
                  text = '待打款';
                } else if (status === 3) {
                  type = 'success';
                  text = '已打款';
                } else if (status === 4) {
                  type = 'danger';
                  text = '已拒绝';
                }
                return (
                  <el-tag effect="plain" type={type} size="medium">
                    {text}
                  </el-tag>
                );
              }
            }}
          />
          <el-table-column
            label="申请时间"
            scopedSlots={{
              default: () => {
                return '2020-08-20 14:05:29';
              }
            }}
          />
          <el-table-column
            label="操作"
            scopedSlots={{
              default: scope => {
                return (
                  <div class="le-table-operate flex align-center">
                    {(() => {
                      if (scope.row.status === 2) {
                        return [
                          [<el-button type="text">打款</el-button>],
                          [<span class="le-line" />],
                          [<el-button type="text">拒绝</el-button>]
                        ];
                      } else if (scope.row.status === 1) {
                        return [
                          [<el-button type="text">通过</el-button>],
                          [<span class="le-line" />],
                          [<el-button type="text">拒绝</el-button>]
                        ];
                      }
                    })()}
                  </div>
                );
              }
            }}
          />
        </el-table>
        <div class="flex le-pagination justify-end">
          <el-pagination
            background={true}
            current-page={self.form.page}
            page-count={self.form.count}
            layout="prev, pager, next, jumper"
          />
        </div>
      </div>
    );
  }
};
</script>

<style scoped lang="less">
@import './../index.less';
.le-table ::v-deep {
  .le-table-row:extend(.font-family-yahei) {
    height: 120px;
    font-size: 14px;
    font-weight: 400;
    color: #262626;
  }
  .le-usercontent {
    padding-left: 12px;
    .le-user {
      margin-bottom: 8px;
    }
  }
  .le-username {
    margin-left: 8px;
  }
  .le-userinfor:extend(.font-family-yahei) {
    width: 156px;
    background: #f3f5f7;
    border-radius: 4px;
    padding: 8px;
    font-size: 12px;
    color: #595959;
    line-height: 16px;
  }
  .le-auxiliaryText:extend(.font-family-yahei) {
    font-size: 12px;
    font-weight: 400;
    color: #8c8c8c;
    line-height: 16px;
  }
}
</style>
